
import '@vuepic/vue-datepicker/dist/main.css'
import { defineComponent } from "vue";
import { FileSize } from "@/shared/services/FileSizeService";
import { ICM21100112_REQ_BODY, ICM21100112_RES, ContentsLanguageList } from "@/shared/type/ICM21100112.type";
import { ICM21100131_REQ_BODY, ICM21100131_RES } from "@/shared/type/ICM21100131.type";
import { ICM21100121_REQ_BODY, ICM21100121_RES } from "@/shared/type/ICM21100121.type";
import { ICM21100141_REQ_BODY, ICM21100141_RES } from "@/shared/type/ICM21100141.type";
import { FILE, FILE_TYPE } from '@/shared/constants/common.const';
import { AttachInfoList } from "@/shared/type/ICM21100112.type";
import { modalController } from "@ionic/vue";
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import { screenMode } from '@/shared/type/Approval.type';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { ModifyCardType } from '@/shared/components/ModifyCard/ModifyCard.type';
import { FileInfo } from '@/shared/type/ICM20100112.type';
import TextEditor from '@/shared/components/TextEditor/TextEditor.vue'

const fileSize = new FileSize();

export default defineComponent({
    name: "icm211001sc01",

    components: {
        baseTab,
        basePan,
        VueDatePicker,
        ModifyCard,
        BreadCrumb,
        SelectOption,
        TextEditor
    },

    data() {
        return {
            headersIcon: [
                { key: "En", name: "common.english" },
                { key: "Km", name: "common.khmer" },
                { key: "Ko", name: "common.korean" },
                // { key: "Ja", name: "common.japanese" },
                // { key: "Zh", name: "common.chinese" },
            ],
            promotionDetailsLangDefaultValue: [
                { languageCode: "common.english", title: "", description: "", contentHtml: "" },
                { languageCode: "common.khmer", title: "", description: "", contentHtml: "" },
                { languageCode: "common.korean", title: "", description: "", contentHtml: "" },
                { languageCode: "common.japanese", title: "", description: "", contentHtml: "" },
                { languageCode: "common.chinese", title: "", description: "", contentHtml: "" },
            ] as ContentsLanguageList[],
            promotionDetails: {
                attachInfoList: [] as AttachInfoList[],
                contentsList: [] as ContentsLanguageList[],
                displayYN: '',
                statusCode: '',
                publishFromDate: new Date(),
                promotionID: '',
                publishToDate: new Date(),
            },
            isDisable: true,
            isInValidInput: false,
            fileInfo: {} as FileInfo,
            getImage: null,
            getFile: null,
            isFileEmty: false,
            isImageEmty: false,
            modifyCard: {} as ModifyCardType,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        };
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        formattedStartDate(): string {
            return this.$datetime.formatVueDatePicker(this.promotionDetails.publishFromDate)
        },

        formattedEndDate(): string {
            return this.$datetime.formatVueDatePicker(this.promotionDetails.publishToDate)
        },

        btnDelete() {
            if (this.$route.query.type === 'edit' && this.isDisable) {
                return true
            }
            return false
        },

        btnList() {
            if (this.$route.query.type === 'edit' && this.isDisable) {
                return true
            }
            return false
        },

        btnCancel() {
            if (!this.isDisable) {
                return true
            }
            return false
        },

        btnEdit() {
            if (this.$route.query.type === 'edit' && this.isDisable) {
                return true
            }
            return false
        },

        btnSave() {
            if ((this.$route.query.type == 'edit' && !this.isDisable) || (this.$route.query.type == 'register')) {
                return true
            }
            return false
        }
    },

    watch: {
        promotionDetailsLangDefaultValue: {
            handler(value) {
                value.forEach((item: { description: string; contentHtml: string; }) => {
                    item.description = this.$validate.formatHtmlToString(item.contentHtml)
                })
            },
            deep: true
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            if (this.$route.query.type == "register") {
                this.$util.editableContent(true)
                this.isDisable = false
            } else {
                this.$util.editableContent(false)
                this.requestPromotionDetails()
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const reqBody = { promotionID: Number(this.$route.query.promotionID) } as ICM21100112_REQ_BODY
                    const res = await this.$request.getRequest("ICM21100112", reqBody, false) as ICM21100112_RES
                    this.$util.editableContent(false);
                    await this.assignValueToTab(this.convertCommonCode(res?.body.contentsList))
                    this.promotionDetails = {
                        attachInfoList: res.body.attachInfoList,
                        contentsList: res.body.contentsList,
                        displayYN: res.body.displayYN,
                        statusCode: res.body.statusCode,
                        publishFromDate: this.$datetime.convertStringToDate(res.body.publishFromDate),
                        promotionID: res.body.promotionID,
                        publishToDate: this.$datetime.convertStringToDate(res.body.publishToDate)
                    };
                    this.requestFileDetail()
                } else {
                    await this.assignValueToTab(this.convertCommonCode(authDetailParsed.contentsList))
                    this.promotionDetails = {
                        attachInfoList: authDetailParsed.attachInfoList,
                        contentsList: authDetailParsed.contentsList,
                        displayYN: authDetailParsed.displayYN,
                        statusCode: authDetailParsed.statusCode,
                        publishFromDate: this.$datetime.convertStringToDate(authDetailParsed.publishFromDate),
                        promotionID: authDetailParsed.promotionID,
                        publishToDate: this.$datetime.convertStringToDate(authDetailParsed.publishToDate)
                    };
                    this.requestFileDetail()
                }
            })
        }
    },

    methods: {
        convertCommonCode(listArr: ContentsLanguageList[]): ContentsLanguageList[] {
            const convertLangCode = listArr.map((item: ContentsLanguageList) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : ''
                return item;
            })
            return convertLangCode
        },

        async requestPromotionDetails() {
            const reqBody = { promotionID: Number(this.$route.query.promotionID) } as ICM21100112_REQ_BODY
            const res = await this.$request.getRequest("ICM21100112", reqBody, false) as ICM21100112_RES
            if (res.header.result) {
                await this.assignValueToTab(this.convertCommonCode(res?.body.contentsList))
                this.promotionDetails = {
                    attachInfoList: res?.body.attachInfoList,
                    contentsList: res?.body.contentsList,
                    displayYN: res?.body.displayYN,
                    statusCode: res?.body.statusCode,
                    publishFromDate: this.$datetime.convertStringToDate(res?.body.publishFromDate),
                    promotionID: res?.body.promotionID,
                    publishToDate: this.$datetime.convertStringToDate(res?.body.publishToDate)
                }
                this.modifyCard = {
                    registerTellerId: res?.body.firstRegisterTellerID,
                    registerTellerName: res?.body.firstRegisterTellerName,
                    registerDate: res?.body.firstRegisterDate,
                    registerTime: res?.body.firstRegisterTime,
                    updatedTellerId: this.promotionDetails.statusCode === '09' ? '' : res?.body.lastChangeTellerID,
                    updatedTellerName: this.promotionDetails.statusCode === '09' ? '' : res?.body.lastChangeTellerName,
                    updatedDate: this.promotionDetails.statusCode === '09' ? '' : res?.body.lastChangeDate,
                    updatedTime: this.promotionDetails.statusCode === '09' ? '' : res?.body.lastChangeTime,
                    deleteTellerId: this.promotionDetails.statusCode === '09' ? res?.body.lastChangeTellerID : '',
                    deleteTellerName: this.promotionDetails.statusCode === '09' ? res?.body.lastChangeTellerName : '',
                    deleteDate: this.promotionDetails.statusCode === '09' ? res?.body.lastChangeDate : '',
                    deleteTime: this.promotionDetails.statusCode === '09' ? res?.body.lastChangeTime : ''
                }
                this.requestFileDetail()
            }
        },

        async requestFileDetail() {
            for (let i = 0; i < this.promotionDetails.attachInfoList.length; i++) {
                if (this.promotionDetails.attachInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
                    const res = await this.$file.getFileInfo(this.promotionDetails.attachInfoList[i].fileId)
                    this.fileInfo.fileName = res?.data.body.originalFileName
                    this.fileInfo.fileSize = res?.data.body.fileSizeString
                }
                if (this.promotionDetails.attachInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
                    const res = await this.$file.getFileInfo(this.promotionDetails.attachInfoList[i].fileId)
                    this.fileInfo.imageName = res?.data.body.originalFileName
                    this.fileInfo.imgSize = res?.data.body.fileSizeString
                    const resImage = await this.$file.previewImage(this.promotionDetails.attachInfoList[i].fileId)
                    this.fileInfo.image = resImage
                }
            }
        },

        async handleRegister() {
            await this.handleFileRegister()
            const reqBody = {
                displayYN: this.promotionDetails.displayYN,
                publishToDate: this.$datetime.convertDateToString(this.promotionDetails.publishToDate),
                contentsList: this.convertLangToCode(JSON.parse(JSON.stringify(this.promotionDetailsLangDefaultValue))),
                publishFromDate: this.$datetime.convertDateToString(this.promotionDetails.publishFromDate),
                attachInfoList: this.promotionDetails.attachInfoList
            } as ICM21100121_REQ_BODY;
            console.log('regis', reqBody);
            const res = await this.$request.getRequest("ICM21100121", reqBody, false) as ICM21100121_RES
            if (res.header.result) {
                this.$router.push("ICM211001SC00");
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },

        async handleUpdate() {
            await this.handleFileUpdate()
            const reqBody = {
                displayYN: this.promotionDetails.displayYN,
                publishToDate: this.$datetime.convertDateToString(this.promotionDetails.publishToDate),
                contentsList: this.convertLangToCode(JSON.parse(JSON.stringify(this.promotionDetailsLangDefaultValue))),
                publishFromDate: this.$datetime.convertDateToString(this.promotionDetails.publishFromDate),
                attachInfoList: this.promotionDetails.attachInfoList,
                promotionID: Number(this.$route.query.promotionID)
            } as ICM21100131_REQ_BODY;
            console.log(reqBody.attachInfoList);
            const res = await this.$request.getRequest("ICM21100131", reqBody, false) as ICM21100131_RES
            if (res.header.result) {
                this.isDisable = true;
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.requestPromotionDetails()
                        modalController.dismiss()
                    }
                });
            }
        },

        async handleDelete() {
            const reqBody = { promotionID: Number(this.$route.query.promotionID) } as ICM21100141_REQ_BODY
            const res = await this.$request.getRequest("ICM21100141", reqBody, false) as ICM21100141_RES
            // this.promotionDetails.attachInfoList.forEach((item) => {
            //   this.$file.updateFileStatus(item.fileId, FILE_TYPE.INACTIVE)
            // })
            if (res.header.result) {
                this.$router.push("ICM211001SC00")
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "delete":
                    if (this.$route.query.type === "edit") {
                        this.$dialog.onOpen({
                            content: this.$t('message.are_you_sure_you_want_to_delete'),
                            dialogMode: 'Question',
                            callback: () => {
                                this.handleDelete()
                                modalController.dismiss()
                            },
                        });
                    } else {
                        this.$router.push("ICM211001SC00");
                    }
                    break;
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        dialogMode: 'Question',
                        callback: () => {
                            if (this.$route.query.type === "edit") {
                                this.$util.editableContent(false)
                                this.isDisable = true
                                this.isInValidInput = false
                                this.requestPromotionDetails()
                            } else {
                                this.$router.push("ICM211001SC00")
                            }
                            modalController.dismiss()
                        },
                    });
                    break;
                case "save":
                    var item: any = this.promotionDetailsLangDefaultValue.find(data => data.languageCode == 'common.english')
                    if (item.description && item.title) {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                this.$util.editableContent(false)
                                if (this.$route.query.type === "edit") {
                                    this.handleUpdate()
                                } else {
                                    this.handleRegister()
                                }
                                modalController.dismiss()
                            },
                        })
                    } else {
                        this.isInValidInput = true
                        this.$util.scrollToElement('inp_error')
                    }
                    break;
                case "edit":
                    this.isDisable = false
                    this.$util.editableContent(true)
                    break;
                default:
                    this.$router.push("ICM211001SC00")
                    break;
            }
        },

        convertLangToCode(listsArr: ContentsLanguageList[]): ContentsLanguageList[] {
            const data = listsArr.map((item: ContentsLanguageList) => {
                item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value")
                return item;
            });
            return data;
        },

        handleUploadImage(event: any) {
            this.isImageEmty = false
            const accectFile = /(\.png)$/i;
            const files = event.target.files;
            const file = files[0];
            if (!accectFile.exec(file.name)) {
                this.$dialog.onOpen({
                    content: this.$t('message.only_png_file_can_be_inserted'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else if (file.size > FILE.SIZE_2MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_2MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getImage = file;
                const size = fileSize.countFileSize(file.size)
                this.fileInfo.imgSize = size
                const reader = new FileReader()
                reader.readAsDataURL(file)
                this.fileInfo.imageName = file.name
                reader.onload = () => {
                    this.fileInfo.image = String(reader.result)
                };
            }
            const duplicatedImage = document.getElementById('btn02 btn_upload') as HTMLInputElement;
            duplicatedImage.value = ''
        },

        removeFile(attachTypeCode: string) {
            switch (attachTypeCode) {
                case FILE_TYPE.FILE:
                    this.isFileEmty = true
                    this.getFile = null
                    this.fileInfo.fileSize = this.fileInfo.fileName = ''
                    break;
                case FILE_TYPE.IMAGE:
                    this.isImageEmty = true
                    this.getImage = null
                    this.fileInfo.imageName = this.fileInfo.imgSize = ''
                    break;
            }
        },

        handleUploadFile(event: any) {
            this.isFileEmty = false
            const files = event.target.files
            const file = files[0]
            if (file.size > FILE.SIZE_10MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_10MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getFile = file
                const size = fileSize.countFileSize(file.size)
                this.fileInfo.fileSize = size
                const reader = new FileReader()
                reader.readAsDataURL(file)
                this.fileInfo.fileName = file.name
            }
            const duplicatedFile = document.getElementById('actual-btn') as HTMLInputElement;
            duplicatedFile.value = ''
        },

        async handleFileUpdate() {
            if (this.getFile !== null && this.getImage !== null && !this.isFileEmty && !this.isImageEmty) {
                if (this.promotionDetails.attachInfoList) {
                    this.promotionDetails.attachInfoList.forEach(async (item) => {
                        await this.$file.updateFileStatus(item.fileId, FILE_TYPE.INACTIVE)
                    })
                }
                this.promotionDetails.attachInfoList = []
                const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    if (res.data.body.fileList[i].keyName === "file1") {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.promotionDetails.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.FILE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                    if (res.data.body.fileList[i].keyName === "file2") {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.promotionDetails.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.IMAGE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                }
            }

            // if (this.getFile === null && this.getImage === null && this.isFileEmty && this.isImageEmty) {
            //   if(this.promotionDetails.attachInfoList) {
            //     this.promotionDetails.attachInfoList.forEach(async (item) => { 
            //       await this.$file.updateFileStatus(item.fileId, FILE_TYPE.INACTIVE) 
            //     })
            //     this.promotionDetails.attachInfoList = []
            //   }
            // }

            if (this.getFile === null && this.isFileEmty) {
                for (let i = 0; i < this.promotionDetails.attachInfoList.length; i++) {
                    if (this.promotionDetails.attachInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
                        await this.$file.updateFileStatus(this.promotionDetails.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
                        const index = this.promotionDetails.attachInfoList.indexOf(this.promotionDetails.attachInfoList[i]);
                        this.promotionDetails.attachInfoList.splice(index, 1);
                    }
                }
            }

            if (this.getImage === null && this.isImageEmty) {
                for (let i = 0; i < this.promotionDetails.attachInfoList.length; i++) {
                    if (this.promotionDetails.attachInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
                        await this.$file.updateFileStatus(this.promotionDetails.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
                        const index = this.promotionDetails.attachInfoList.indexOf(this.promotionDetails.attachInfoList[i]);
                        this.promotionDetails.attachInfoList.splice(index, 1);
                    }
                }
            }

            if (this.getFile !== null && !this.isFileEmty) {
                if (this.promotionDetails.attachInfoList.length === 0) {
                    const res = await this.$file.uploadFile([this.getFile], "ICM")
                    for (let i = 0; i < res.data.body.fileList.length; i++) {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.promotionDetails.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.FILE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                } else if (this.promotionDetails.attachInfoList.length > 0) {
                    if (this.isFileAlreadyExisted(FILE_TYPE.FILE)) {
                        const res = await this.$file.uploadFile([this.getFile], "ICM")
                        for (let index = 0; index < this.promotionDetails.attachInfoList.length; index++) {
                            if (this.promotionDetails.attachInfoList[index].attachTypeCode === FILE_TYPE.FILE) {
                                await this.$file.updateFileStatus(this.promotionDetails.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                                for (let i = 0; i < res.data.body.fileList.length; i++) {
                                    await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                                        Object.assign(this.promotionDetails.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.FILE, filePath: v.data.body.filePath })
                                    )
                                }
                            }
                        }
                    } else {
                        const res = await this.$file.uploadFile([this.getFile], "ICM")
                        for (let i = 0; i < res.data.body.fileList.length; i++) {
                            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                            this.promotionDetails.attachInfoList.push({
                                filePath: resFileInfo.data.body.filePath,
                                attachTypeCode: FILE_TYPE.FILE,
                                fileId: res.data.body.fileList[i].fileId
                            })
                        }
                    }
                }
            }
            if (this.getImage !== null && !this.isImageEmty) {
                if (this.promotionDetails.attachInfoList.length === 0) {
                    const res = await this.$file.uploadFile([this.getImage], "ICM")
                    for (let i = 0; i < res.data.body.fileList.length; i++) {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.promotionDetails.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.IMAGE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                } else if (this.promotionDetails.attachInfoList.length > 0) {
                    if (this.isFileAlreadyExisted(FILE_TYPE.IMAGE)) {
                        const res = await this.$file.uploadFile([this.getImage], "ICM")
                        for (let index = 0; index < this.promotionDetails.attachInfoList.length; index++) {
                            if (this.promotionDetails.attachInfoList[index].attachTypeCode === FILE_TYPE.IMAGE) {
                                await this.$file.updateFileStatus(this.promotionDetails.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                                for (let i = 0; i < res.data.body.fileList.length; i++) {
                                    await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                                        Object.assign(this.promotionDetails.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.IMAGE, filePath: v.data.body.filePath })
                                    )
                                }
                            }
                        }
                    } else {
                        const res = await this.$file.uploadFile([this.getImage], "ICM")
                        for (let i = 0; i < res.data.body.fileList.length; i++) {
                            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                            this.promotionDetails.attachInfoList.push({
                                filePath: resFileInfo.data.body.filePath,
                                attachTypeCode: FILE_TYPE.IMAGE,
                                fileId: res.data.body.fileList[i].fileId
                            })
                        }
                    }
                }
            }
        },

        async handleFileRegister() {
            if (this.getFile !== null && this.getImage !== null) {
                const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    if (res.data.body.fileList[i].keyName === "file1") {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.promotionDetails.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.FILE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                    if (res.data.body.fileList[i].keyName === "file2") {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.promotionDetails.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.IMAGE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                }
            } else if (this.getFile !== null && this.getImage === null) {
                const res = await this.$file.uploadFile([this.getFile], 'ICM')
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                    this.promotionDetails.attachInfoList.push({
                        filePath: resFileInfo.data.body.filePath,
                        attachTypeCode: FILE_TYPE.FILE,
                        fileId: res.data.body.fileList[i].fileId
                    })
                }
            } else if (this.getFile === null && this.getImage !== null) {
                const res = await this.$file.uploadFile([this.getImage], 'ICM')
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                    this.promotionDetails.attachInfoList.push({
                        filePath: resFileInfo.data.body.filePath,
                        attachTypeCode: FILE_TYPE.IMAGE,
                        fileId: res.data.body.fileList[i].fileId
                    })
                }
            }
        },

        isFileAlreadyExisted(fileContentType: FILE_TYPE.FILE | FILE_TYPE.IMAGE) {
            const isFileAlreadyExisted = this.promotionDetails.attachInfoList.some(obj => Object.values(obj).includes(fileContentType))
            return isFileAlreadyExisted
        },

        async assignValueToTab(contentList) {
            for (let i = 0; i < contentList.length; i++) {
                this.promotionDetailsLangDefaultValue.map((item) => {
                    if (item.languageCode === contentList[i].languageCode) {
                        item.title = contentList[i].title
                        item.description = contentList[i].description
                        item.contentHtml = contentList[i].contentHtml
                    } else if (item.contentHtml === '') {
                        item.contentHtml = '<p></p>'
                    }
                })
            }
        },
    },
});
